import React from 'react';
import {globalContext} from '../Context/Context';
import {Link} from 'react-router-dom';
import PropTypes from 'prop-types';
class Checkout extends React.Component {
    constructor(props){
        super(props)
        this.state={
            Total:''
        }
    }
    async componentDidMount(){
        this.context.checkIfLogin();
        this.context.getCartItemsFromDatabase();
        let priceTotal = await this.context.state.CartItems.map((ele) => {
            if(ele.quantity > 1){
               return ele.price * ele.quantity
            }else{
                return ele.price
            }
        });
        if(priceTotal.length > 1){
            let total = await priceTotal.reduce((a,c) => a + c);
                this.setState({
                    Total:(Math.round(total * 100) / 100).toFixed(2)
                });
        }else{
            let total = priceTotal[0];
            this.setState({
                Total:total
            })
        }
    }
    render() { 
        return ( 
            <div>
                <nav className='HomePageNav'>
                    <h3>{this.context.state.NavHeading}</h3>
                        <div className='HomePageLinkCont'>
                            <Link to='/Cart'>Cart</Link>
                            <Link to='/'>Shopping</Link>
                            <button 
                            style={{display:this.context.state.LoggedIn ? '' : 'none'}} 
                            className='LogoutBtn'
                            onClick={this.context.logoutClick}
                            >Logout</button>
                        </div>
                </nav>
                <div className='MainCheckoutContainer'>
                    <div className='container CheckoutContainer'>
                        <ul className='CheckoutUl'>
                            {this.context.state.CartItems.map((ele) => {
                                return(
                                    <li key={ele._id}>
                                        <p>{ele.name}</p>
                                        <p>Qty: {ele.quantity}</p>
                                        <p>${(Math.round(ele.price * 100) / 100).toFixed(2)}</p>
                                    </li>
                                )
                            })}
                        </ul>
                        <div>
                            <h2>Total:</h2>
                            <h3>${this.state.Total}</h3>
                        </div>
                    </div>
                    <button
                    onClick={this.context.checkoutClick}
                    className='btn-primary CheckoutBtn'>Complete Order</button>
                    <div style={{display:this.context.state.CheckedOutOrder ? '' : 'none'}} className="container CheckedOutAlert alert alert-success" >
                        Order Confirmed, Thank You!
                    </div>
                </div>
            </div>
         );
    }
}
Checkout.contextType = globalContext;

// Proptypes
Checkout.propTypes = {
    CartItems:PropTypes.array,
    NavHeading:PropTypes.string,
    LoggedIn:PropTypes.bool,
    logoutClick:PropTypes.func,
    checkoutClick:PropTypes.func,
    CheckedOutOrder:PropTypes.bool,
};
export default Checkout;