import React from 'react';
import {Link} from 'react-router-dom';
import {globalContext} from '../Context/Context';
import PropTypes from 'prop-types';
class CreateAccount extends React.Component {
    componentDidMount(){
        this.context.checkIfLogin();
    }
    render() { 
        return ( 
             <div>
                <nav className='HomePageNav'>
                    <h3>{this.context.state.NavHeading}</h3>
                        <div className='HomePageLinkCont'>
                            <Link to='/'>Shopping</Link>
                            <Link style={{display:this.context.state.LoggedIn ? 'none' : ''}} to='/Login'>Login</Link>
                            <Link  style={{display:this.context.state.LoggedIn ? '' : 'none'}} to='/Cart'>Cart</Link>
                            <button 
                            style={{display:this.context.state.LoggedIn ? '' : 'none'}} 
                            className='LogoutBtn'
                            onClick={this.context.logoutClick}
                            >Logout</button>
                        </div>
                </nav>
                <form 
                className='container RegisterForm'
                onSubmit={(e) => {this.context.registerAccountSubmit(e)}}
                >
                    <div className='RegisterUsernameDiv'>
                        <label htmlFor='Username'>Username</label>
                        <input 
                        type='Text' 
                        required 
                        placeholder='Enter Username' 
                        value={this.context.state.RegisterUsername}
                        onChange={(e) => {this.context.registerUsernameChange(e)}} 
                        name='RegisterUsername'
                        ></input>
                    </div>
                    <div className='RegisterEmailDiv'>
                        <label htmlFor='Email'>Email</label>
                        <input 
                        type='Email' 
                        required 
                        placeholder='Enter Email' 
                        value={this.context.state.RegisterEmail}
                        onChange={(e) => {this.context.registerEmailChange(e)}} name='RegisterEmail'
                        ></input>
                    </div>
                    <div className='RegisterPasswordDiv'>
                        <label htmlFor='Password'>Password</label>
                        <input 
                        type='Password' 
                        required 
                        placeholder='Enter Password' 
                        value={this.context.state.RegisterPassword}
                        onChange={(e) => {this.context.registerPasswordChange(e)}} 
                        name='RegisterPassword'
                        ></input>
                    </div>
                        <ul className='RegisterUl'>
                            <li
                            style={{textDecoration:this.context.state.PasswordLength ? 'Line-Through' : '', }}
                            >Password is at least 8 characters long</li>
                            <li
                            style={{
                            textDecoration:this.context.state.PasswordCapitalLetter ? 'Line-Through' : ''
                            }}
                            >Contains as least one capital letter</li>
                            <li
                            style={{
                                textDecoration:this.context.state.PasswordSpecialChar ? 'Line-Through' : '',
                                }}
                            >Contains a special character</li>
                        </ul>
                    <div className='RegisterConfirmPasswordDiv'>
                        <label htmlFor='Password'>Password</label>
                        <input 
                        type='Password' 
                        required 
                        placeholder='Re-enter Password' 
                        value={this.context.state.RegisterConfirmPassword}
                        onChange={(e) => {this.context.registerConfirmPasswordChange(e)}} 
                        name='RegisterConfirmPassword'
                        ></input>
                    </div>
                    <button type='Submit' className='btn-primary RegisterButton'>Login</button>
                </form>
                <div style={{display:this.context.state.RegisterFailed ? '' : 'none'}} className="container RegisterWarning alert alert-danger" >
                        {this.context.state.RegisterFailedText}
                        <button onClick={this.context.registerFailedClick} style={{position:'absolute', right:'0', marginRight:'20px'}} className="btn-close "></button>
                    </div>
                    <div style={{display:this.context.state.RegisterSuccess ? '' : 'none'}} className="container RegisterWarning alert alert-success" >
                        Account Created
                        <button onClick={this.context.registerSuccessClick} style={{position:'absolute', right:'0', marginRight:'20px'}} className="btn-close "></button>
                    </div>
            </div>
         );
    }
}
CreateAccount.contextType = globalContext;

// Proptypes
CreateAccount.propTypes = {
    NavHeading:PropTypes.string,
    LoggedIn:PropTypes.bool,
    logoutClick:PropTypes.func,
    registerAccountSubmit:PropTypes.func,
    RegisterUsername:PropTypes.string,
    registerUsernameChange:PropTypes.func,
    RegisterEmail:PropTypes.string,
    registerEmailChange:PropTypes.func,
    RegisterPassword:PropTypes.string,
    registerPasswordChange:PropTypes.func,
    PasswordLength:PropTypes.bool,
    PasswordCapitalLetter:PropTypes.bool,
    PasswordSpecialChar:PropTypes.bool,
    RegisterConfirmPassword:PropTypes.string,
    registerConfirmPasswordChange:PropTypes.func,
    RegisterFailed:PropTypes.bool,
    RegisterFailedText:PropTypes.string,
    registerFailedClick:PropTypes.func,
    RegisterSuccess:PropTypes.bool,
    registerSuccessClick:PropTypes.func
};
export default CreateAccount;